
html{box-sizing:border-box
}
body{font-family:"ratio",Helvetica,Arial,sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;letter-spacing:0.13em;text-align:center;background-color:#333439;color:#E0E0E0;min-width:500px;min-height:100%;height:100%;margin:0
}
button{-webkit-appearance:none;background:transparent;font-family:"ratio",Helvetica,Arial,sans-serif;font-size:100%;letter-spacing:0.15em;color:#E0E0E0;border:0;outline:0
}
@font-face{font-family:'ratio';src:url("../assets/fonts/ratio.woff2") format("woff2"),url("../assets/fonts/ratio.woff") format("woff"),url("../assets/fonts/ratio.ttf") format("truetype");font-weight:normal;font-style:normal
}
.yellow{color:#facb16
}
a.yellow:hover,a.yellow:focus{color:#FFE489
}
.section{min-height:530px;text-align:left
}
#app{position:relative;min-height:100%
}
.router-page{padding-bottom:60px
}
.parallax{background-image:url("../assets/graphics/welcome-background.png");background-attachment:fixed;background-repeat:no-repeat;background-size:cover
}
.live{display:-ms-inline-flexbox;display:inline-flex;-ms-flex-align:center;align-items:center;line-height:normal;background-color:#af0000;padding:5px 10px;border-radius:10px;white-space:nowrap
}
.svg-glyph_live{fill:#E0E0E0
}
a,p{text-decoration:none;color:#E0E0E0;margin:0;outline:none
}
ul{cursor:default;margin:0;padding:0
}
h1,h2,h3{font-weight:lighter
}
h2{text-transform:capitalize
}
h2,h3{color:#facb16;font-size:26px;margin:0
}
h3{font-size:16px;margin:5px 0
}



